.wrap-flex {
  display: flex;
  justify-content: space-between;
}
.main-sidebar {
  width: 272px;
  &__wrap {
    height: 100%;
    background: #121212;
    padding: 30px 15px;
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.1);;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #ffffff;
      border-radius: 3px;
    }
  }
  &__bg {
    span:nth-child(1) {
      content: '';
      position: absolute;
      top: 0;
      right: -420px;
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: rgba(31, 188, 255, 0.19);
      filter: blur(107px);
      pointer-events: none;
    }
    span:nth-child(2) {
      content: '';
      position: absolute;
      left: -250px;
      bottom: 0;
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: rgba(31, 188, 255, 0.19);
      filter: blur(107px);
      pointer-events: none;
    }
    span:nth-child(3) {
      content: '';
      position: absolute;
      left: -250px;
      bottom: -250px;
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: rgba(31, 188, 255, 0.19);
      filter: blur(107px);
      pointer-events: none;
    }
  }
  &__content {
    position: relative;
    z-index: 2;
  }
  &__domain {
    background: rgba(0, 0, 0, 0.20);
    padding: 14px;
    &-title {
      font-size: 10px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #7C7C7C;
    }
    &-text {
      display: flex;
      align-items: center;
      margin-top: 3px;
      span {
        font-size: 20px;
        line-height: 120%;
        color: #ffffff;
        margin-left: 6px;
      }
    }
    .domain-icon {
      width: 16px;
      img {
        width: 100%;
      }
    }
    .domain-icon2 {
      width: 16px;
      margin-left: 6px;
      img {
        width: 100%;
      }
    }
  }
  &__items {
    margin-top: 15px;
  }
  &__item {
    border-bottom: 1px solid rgba(255, 255, 255, 0.10);
    padding: 15px 0;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
    }
    &-show--active {
      .main-sidebar__item-arrow {
        transform: rotate(-180deg);
      }
    }
    &-title {
      display: flex;
      align-items: center;
      i {
        width: 16px;
        margin-right: 5px;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 16px;
        line-height: 120%;
        color: #ffffff;
      }
    }
    &-arrow {
      flex-shrink: 0;
      width: 11px;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: all 0.5s;
      margin-left: 10px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-hidden {
      padding: 20px 0 0 21px;
      display: none;
    }
    &-links {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
    }
    &-link {
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 120%;
      color: rgba(255, 255, 255, 0.60);
      &:hover {
        color: #F2F2F2;
      }
      span:nth-child(2) {
        font-size: 10px;
        line-height: 105%;
        font-weight: 700;
        color: #ffffff;
        border: 1px solid rgba(196, 196, 196, 0.20);
        border-radius: 10px;
        padding: 2px 4px;
        margin-left: 8px;
      }
      .gray {
        background: rgba(130, 130, 130, 0.50);
      }
      .red {
        border: 1px solid rgba(255, 255, 255, 0.30);
        background: #DF5644;
      }
      .blue {
        border: 1px solid rgba(255, 255, 255, 0.30);
        background: #29A2B4;
      }
    }
    .active {
      color: #5B94FF;
      &:hover {
        color: #5B94FF;
      }
    }
  }
  .change {
    &-show {
      display: flex;
      align-items: center;
      cursor: pointer;
      &:after {
        display: none;
      }
      span {
        font-size: 15px;
        line-height: 120%;
        color: #ffffff;
      }
      i {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        margin-left: 3px;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 21px;
          height: 21px;
          background: rgba(255, 255, 255, 0.30);
          box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.60);
          border-radius: 4px;
          transition: all 0.5s;
          opacity: 0;
          pointer-events: none;
        }
      }
    }
    &-hide {
      left: 0;
      right: unset;
      width: 250px;
    }
  }
  .period {
    position: relative;
    margin-left: 5px;
    span {
      &:not(:first-child) {
        margin-left: 0px;
      }
    }
    &-show {
      svg path, rect {
        transition: all 0.5s;
      }
      &:hover {
        svg path, rect {
          stroke: #ffffff;
        }
      }
    }
    &-hide {
      position: absolute;
      top: 100%;
      right: -80px;
      width: 250px;
      padding-top: 15px;
    }
    &-wrap {
      background: #2e2e2e;
      padding: 15px;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        top: -18px;
        right: 25px;
        border: 10px solid transparent;
        border-bottom: 10px solid #2e2e2e;
        pointer-events: none;
      }
    }
  }
}
.main-content {
  width: calc(100% - 272px);
  padding: 30px 0 120px 40px;
}
.sidebar-tariff {
  display: grid;
  grid-template-columns: 100%;
  gap: 12px;
  margin-top: 36px;
  &__item {
    &-flex {
      display: flex;
      justify-content: space-between;
    }
    &-group:not(:first-child) {
      margin-left: 15px;
    }
    &-title {
      font-size: 10px;
      line-height: 120%;
      font-weight: 600;
      color: rgba(255, 255, 255, 0.40);
    }
    &-text {
      display: flex;
      align-items: center;
      margin-top: 3px;
      span {
        font-size: 15px;
        line-height: 120%;
        color: #ffffff;
        &:not(:first-child) {
          margin-left: 5px;
        }
      }
      .change {
        width: 10px;
        margin-left: 8px;
        img {
          width: 100%;
        }
      }
    }
    &-checkbox {
      display: flex;
      align-items: center;
      position: relative;
      margin-top: 5px;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
        &:checked ~ label {
          background: #0055F6;
          &:after {
            left: calc(100% - 15px);
            background: #ffffff;
          }
        }
      }
      label {
        width: 35px;
        height: 18px;
        background: #000000;
        border-radius: 20px;
        transition: all 0.5s;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 3px;
          transform: translate(0, -50%);
          width: 12px;
          height: 12px;
          background: #343434;
          border-radius: 50%;
          transition: all 0.5s;
        }
      }
    }
  }
}
.domain {
  padding: 20px 0;
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  &__left {
    width: 260px;
    margin-right: 80px;
  }
  &__right {
    width: calc(100% - 340px);
  }
  &__subtitle {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    &-text {
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
      color: #C9C9C9;
    }
    &-icons {
      display: flex;
      align-items: center;
      margin-left: 15px;
      span {
        width: 9.384px;
        height: 9.384px;
        border-radius: 50%;
        &:not(:first-child) {
          margin-left: 8px;
        }
      }
    }
  }
  &__item {
    padding: 20px 0;
    &:first-child {
      padding-top: 0;
    }
    &:not(:last-child) {
      border-bottom: 1px solid #2F2F31;
    }
    &-grid {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
      gap: 30px;
    }
    &-group {
      max-width: 165px;
    }
    &-title {
      font-size: 10px;
      line-height: 120%;
      font-weight: 700;
      color: #666;
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #CDCDCD;
      margin-top: 5px;
    }
  }
}
.main {
  &-cards {
    display: grid;
    grid-template-columns: repeat(4, calc(100% / 4 - 15px / 4));
    gap: 5px;
    margin-top: 30px;
  }
  &-card {
    display: flex;
    background-image: url("../img/main-card__ing.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 25px 15px 15px 15px;
    position: relative;
    @media screen and (min-width: 1351px) {
      &:nth-child(4n+4) {
        .dashboard-card__active-hide {
          left: -135px;
          transform: translate(0, 0);
        }
        .dashboard-card__active-wrap {
          &:after {
            left: calc(100% - 40px);
            transform: translate(0, 0);
          }
        }
      }
    }
    .dashboard-card__active {
      position: absolute;
      top: 7px;
      right: 7px;
      margin-left: 0;
    }
    .dashboard-card__active-hide {
      left: 50%;
      transform: translate(-50%, 0);
      width: 150px;
    }
    .dashboard-card__active-wrap {
      font-size: 12px;
      a {
        font-size: 14px;
      }
      &:after {
        left: 50%;
        transform: translate(-50%, 0);
      }
    }
    &__left {
      max-width: 80px;
      margin-right: 15px;
    }
    &__title {
      font-size: 10px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #7C7C7C;
    }
    &__number {
      display: flex;
      position: relative;
    }
    &__number2 {
      display: inline-flex;
      position: relative;
      margin-top: 7px;
      span:nth-child(1) {
        font-size: 30px;
        line-height: 105%;
        font-weight: 600;
        color: #ffffff;
      }
      span:nth-child(2) {
        height: 19px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 10px;
        line-height: 105%;
        font-weight: 700;
        color: #ffffff;
        border: 1px solid rgba(196, 196, 196, 0.20);
        border-radius: 20px;
        padding: 3px 6px;
        margin-left: 3px;
      }
      .blue {
        background: #29A2B4;
      }
      .orange {
        background: #DF5644;
      }
      .gray {
        background: rgba(130, 130, 130, 0.50);
      }
    }
    &__number3 {
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 120%;
      color: #5E5E5E;
      margin-top: 7px;
      span {
        font-weight: 700;
        margin-left: 6px;
      }
    }
  }
  &-chart__number {
    span {
      display: block;
      font-size: 10px;
      line-height: 120%;
      text-transform: uppercase;
      color: #303030;
      padding: 3px 0;
      border-bottom: 1px solid #303030;
    }
    span:not(:last-child) {

    }
  }
  &-chart__img {
    margin: 7px 0;
    img {
      width: 100%;
    }
  }
  &__flex {
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
  }
  &__left {
    position: relative;
    width: 440px;
    height: 415px;
    background-image: url("../img/radar-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    .dashboard-card__active {
      position: absolute;
      top: 12px;
      right: 12px;
      margin-left: 0;
    }
    .dashboard-card__active-hide {
      left: 50%;
      transform: translate(-50%, 0);
      width: 150px;
    }
    .dashboard-card__active-wrap {
      font-size: 12px;
      a {
        font-size: 14px;
      }
      &:after {
        left: 50%;
        transform: translate(-50%, 0);
      }
    }
  }
}
.statistics {
  padding: 20px 0;
  .content-inner {
    display: block;
    min-height: 100%;
    border-top: unset;
    margin-top: 0;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  &__desc {
    width: 230px;
    background-image: url("../img/statistics-tab__bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 2px;
    padding: 20px;
    margin-right: 50px;
  }
  &__chart {
    width: calc(100% - 280px);
  }
  &__title {
    font-size: 14px;
    line-height: 120%;
    color: rgba(255, 255, 255, 0.50);
  }
  &__info {
    display: inline-flex;
    margin-top: 20px;
  }
  &__icon {
    width: 15px;
    img {
      width: 100%;
    }
  }
  &__number {
    font-size: 45px;
    line-height: 105%;
    font-weight: 600;
    color: #ffffff;
    margin-left: 3px;
  }
  &__number2 {
    span {
      font-size: 12px;
      line-height: 105%;
      font-weight: 700;
      color: #ffffff;
      background: rgba(255, 255, 255, 0.30);
      border-radius: 10px;
      padding: 2px 6px;
      margin-left: 3px;
    }
  }
  &__period {
    margin-top: 30px;
    &-title {
      font-size: 10px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #ffffff;
    }
    &-text {
      font-size: 12px;
      line-height: 120%;
      color: #ffffff;
      margin-top: 4px;
    }
  }
  &__tabs-head {
    display: flex;
    align-items: center;
  }
  .tab {
    font-size: 12px;
    color: #AFAFAF;
    &:not(:first-child) {
      margin: 0 0 0 12px;
    }
    &--active {
      color: #ffffff;
      border-bottom: 1px solid #ffffff;
    }
    &:after {
      display: none;
    }
  }
  &-table {
    thead {
      tr {
        th {
          font-weight: 600;
          color: #C9C9C9;
        }
        th:nth-child(1) {
          width: 28px;
        }
        th:nth-child(2) {
          width: 135px;
        }
        th:nth-child(3) {
          width: 240px;
        }
        th:nth-child(4) {
          width: 115px;
        }
        th:nth-child(5) {
          width: 310px;
        }
      }
    }
    tbody {
      .active {
        td {
          font-weight: 800;
        }
      }
      tr {
        &:hover {
          background: rgba(255, 255, 255, 0.06);
          td {
            color: #ffffff;
          }
        }
        td {
          font-size: 16px;
          font-weight: 400;
          color: #AFAFAF;
        }
      }
      .after-none {
        &:after {
          display: none;
        }
      }
    }
    .view {
      &.open {
        .statistics-table__arrow {
          transform: rotate(-180deg);
        }
      }
    }
    &__arrow {
      transition: all 0.3s;
    }
  }
  &-table__item {
    position: relative;
    &-hide {
      position: absolute;
      top: 100%;
      left: 0;
      width: 167px;
      padding-top: 15px;
    }
    &-wrap {
      background: #2e2e2e;
      padding: 15px;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        top: -18px;
        right: 25px;
        border: 10px solid transparent;
        border-bottom: 10px solid #2e2e2e;
        pointer-events: none;
      }
    }
    &-link {
      display: block;
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 10px;
      }
      &:hover {
        color: #AFAFAF;
      }
    }
  }
  .number {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -14px;
      transform: translate(0, -50%);
      width: 6px;
      height: 6px;
      background: rgba(169, 169, 169, 0.10);
      border-radius: 50%;
    }
    &--active {
      &:after {
        background: #00F619;
      }
    }
  }
  .reviewed {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    line-height: 120%;
    color: #AFAFAF;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 1px;
    padding: 2px 4px;
    min-width: 60px;
  }
  .reviewed-not {
    min-width: 85px;
    color: #FBE597;
  }
  .positive {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    line-height: 120%;
    color: #70FFD4;
    background: rgba(112, 255, 212, 0.26);
    border-radius: 1px;
    padding: 2px 4px;
    min-width: 85px;
  }
  .pagination {
    margin-top: 30px;
  }
  &__btns {
    display: inline-flex;
    align-items: center;
    border: 1px solid #1E1E1E;
    border-radius: 2px;
    padding: 3px;
    margin-top: 20px;
  }
  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 106px;
    height: 40px;
    cursor: pointer;
    span {
      font-size: 14px;
      line-height: 120%;
      color: rgba(255, 255, 255, 0.41);
    }
    &--active {
      background: #878787;
      span {
        color: #000000;
      }
    }
  }
  &-table__items {
    display: inline-flex;
    align-items: center;
    background: #000;
    border-radius: 1px;
  }
  &-table__item {
    display: flex;
    align-items: center;
    padding: 5px;
    &:not(:last-child) {
      border-right: 1px solid #161616;
    }
    i {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    span {
      font-size: 10px;
      line-height: 105%;
      font-weight: 700;
      text-transform: uppercase;
      color: #555;
      margin-top: 3px;
      margin-left: 3px;
    }
  }
  &__items {
    max-width: 740px;
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 30px;
  }
  &__item {
    &-title {
      font-size: 10px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #7C7C7C;
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #CDCDCD;
      margin-top: 5px;
    }
    &-img {
      width: 150px;
      margin-top: 5px;
      img {
        width: 100%;
      }
    }
  }
  .dashboard-card__active {
    display: inline-flex;
    margin: 20px 0 0 0;
    &-show {
      font-size: 18px;
      color: #EAEAEA;
      span {
        border-color: #4C4C4C;
        color: #4C4C4C;
      }
      &:hover {
        color: #ffffff;
        span {
          border-color: #ffffff;
          color: #ffffff;
        }
      }
    }
  }
  .overflow-y {
    overflow-y: hidden;
    overflow-x: auto;
    &::-webkit-scrollbar {
      height: 3px;
    }
    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.1);;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #ffffff;
      border-radius: 3px;
    }
    table {
      width: 1098px;
    }
    .accordion-table {
      width: 1098px;
    }
  }
  &-tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
}
.change {
  position: relative;
  &-show {
    display: inline-block;
    position: relative;
    &:hover {
      &:after {
        opacity: 1;
        pointer-events: all;
      }
      svg path {
        stroke: #ffffff;
      }
    }
    svg path {
      transition: all 0.5s;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 21px;
      height: 21px;
      background: rgba(255, 255, 255, 0.30);
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.60);
      border-radius: 4px;
      transition: all 0.5s;
      opacity: 0;
      pointer-events: none;
    }
  }
  &-hide {
    position: absolute;
    top: 100%;
    right: 0;
    width: 272px;
    padding-top: 15px;
  }
  &-wrap {
    background: #2e2e2e;
    padding: 15px;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      top: -18px;
      right: 25px;
      border: 10px solid transparent;
      border-bottom: 10px solid #2e2e2e;
      pointer-events: none;
    }
  }
  .tariff {
    padding-bottom: 0;
    &:after {
      display: none;
    }
  }
}
.item--active {
  .change-show {
    &:after {
      opacity: 1;
      pointer-events: all;
    }
    svg path {
      stroke: #ffffff;
    }
  }

}
.chart {
  width: 100%;
  img {
    width: 100%;
    margin-top: 20px;
  }
  
}
.mail-substitution {
  padding: 20px 0;
  &__content {
    background: #1B1C1E;
    padding: 24px 24px 40px 24px;
    margin-top: 20px;
  }
  &__info {
    max-width: 832px;
    display: flex;
    align-items: center;
    border: 1px dashed #303030;
    border-radius: 1px;
    padding: 16px 30px;
    span:nth-child(1) {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 35px;
      height: 35px;
      background: rgba(255, 255, 255, 0.05);
      border-radius: 50%;
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
    }
    span:nth-child(2) {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
      margin-left: 16px;
    }
  }
  &__link {
    font-family: 'Inter';
    display: block;
    font-size: 16px;
    line-height: 120%;
    color: #979797;
    margin-top: 12px;
  }
  &__btn {
    width: 190px;
    height: 45px;
    background: transparent;
    border: 1px solid #0055F6;
    margin-top: 18px;
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
      color: #ffffff;
    }
    &:hover {
      background: #0055F6;
    }
  }
}
.osint {
  padding: 20px 0 0 0;
  &__content {
    max-width: 710px;
    margin-top: 20px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__item {
    &-title {
      font-size: 10px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #7C7C7C;
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #CDCDCD;
      margin-top: 5px;
    }
  }
}
.accordion-table {
  margin-top: 20px;
  &__thead {
    display: flex;
    align-items: center;
    background: #000;
  }
  &__th {
    font-size: 14px;
    line-height: 120%;
    font-weight: 600;
    color: #C9C9C9;
    padding: 12px 5px;
    &:first-child {
      padding-left: 14px;
    }
    &:last-child {
      padding-right: 14px;
    }
  }
  &__tr {
    border-bottom: 1px solid #2A2A2D;
  }
  &__show {
    display: flex;
    align-items: center;
    cursor: pointer;
    &:hover {
      background: rgba(255, 255, 255, 0.06);
      color: #ffffff;
    }
  }
  &__show--active {
    .accordion-table__arrow {
      transform: rotate(-180deg);
    }
  }
  &__hide {
    display: none;
  }
  &__td {
    font-size: 16px;
    line-height: 120%;
    color: #A9A9A9;
    padding: 12px 5px;
    &:first-child {
      padding-left: 14px;
    }
    &:last-child {
      padding-right: 14px;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      width: 698px;
      height: 1px;
      background: #121213;
    }
    &:last-child {
      &:after {
        display: none;
      }
    }
  }
  &__arrow {
    margin: auto;
    width: 12px;
    svg {
      width: 100%;
      height: auto;
    }
    transition: all 0.3s;
  }
  &__file {
    display: flex;
    align-items: center;
    i {
      max-width: 15px;
      img {
        max-width: 100%;
      }
    }
    span {
      margin-left: 8px;
    }
  }
}

@media screen and (max-width: 1350px) {
  .main {
    &-cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 10px / 3));
    }
    &-card {
      @media screen and (min-width: 1101px) {
        &:nth-child(3n+3) {
          .dashboard-card__active-hide {
            left: -135px;
            transform: translate(0, 0);
          }
          .dashboard-card__active-wrap {
            &:after {
              left: calc(100% - 40px);
              transform: translate(0, 0);
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .domain {
    &__item {
      &-grid {
        grid-template-columns: repeat(2, calc(50% - 50px / 2));
        gap: 50px;
      }
      .opacity {
        display: none;
      }
    }
  }
  .statistics {
    &-table {
      tbody {
        tr {
          td {
            font-size: 14px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .main-sidebar {
    position: fixed;
    z-index: 3;
    top: 73px;
    left: -105%;
    width: 100%;
    height: 100%;
    transition: all 0.5s;
    &--active {
      left: 0;
      .main-sidebar__bg2 {
        opacity: 1;
      }
    }
    &__bg2 {
      position: fixed;
      top: 73px;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.60);
      backdrop-filter: blur(2.5px);
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
    }
    &__wrap {
      width: 272px;
      height: 100%;
    }
  }
  .main-content {
    width: 100%;
    padding: 40px 0 120px;
  }
  .domain {
    &__content {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 30px 0;
    }
    &__right {
      width: 100%;
    }
    &__item {
      &-grid {
        grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
        gap: 30px;
      }
      .opacity {
        display: block;
      }
    }
  }
  .main {
    &-cards {
      grid-template-columns: repeat(4, calc(100% / 4 - 15px / 4));
    }
    &-card {
      &:nth-child(4n+4) {
        .dashboard-card__active-hide {
          left: -135px;
          transform: translate(0, 0);
        }
        .dashboard-card__active-wrap {
          &:after {
            left: calc(100% - 40px);
            transform: translate(0, 0);
          }
        }
      }
      &__number2 {
        span:nth-child(1) {
          font-size: 20px;
        }
        span:nth-child(2) {
          height: 16px;
          font-size: 8px;
        }
      }
      &__number3 {
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .main {
    .dashboard-card__active {
      &-hide {
        top: unset;
        bottom: 100%;
        padding: 0 0 15px 0;
      }
    }
  }
  .statistics {
    .dashboard-card__active {
      &-hide {
        left: 100%;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main-sidebar {
    top: 270px;
    &__bg2 {
      top: 270px;
    }
  }
}