.navigation {
  padding: 50px 0;
  &-list {
    li a {
      font-size: 20px;
      line-height: 120%;
      color: #E1E1E1;
    }
    li:not(:first-child) {
      margin-top: 15px;
    }
    li a:hover {
      opacity: 0.5;
    }
  }
}
.body-bg {
  background: #747474;
}
.wrap {
  overflow: hidden;
}
.item {
  .hide {
    z-index: 5;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &--active {
    .hide {
      opacity: 1;
      pointer-events: all;
    }
  }
}
.btn-blue {
  background: #0055F6;
  span {
    font-family: 'Roboto Flex';
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    color: #FFF;
  }
  &:hover {
    background: #0039b3;
  }
}
.pages {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 120%;
  color: #A7A7A7;
  span {
    padding: 0 3px;
  }
}
.pagination {
  &__items {
    display: flex;
    align-items: center;
  }
  &__item {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 120%;
    color: #979797;
    &:hover {
      color: #ffffff;
    }
    &--active {
      color: #ffffff;
      border: 1px solid rgba(128, 128, 128, 0.30);
      background: rgba(255, 255, 255, 0.05);
    }
  }
  &__prev,
  &__next {
    svg path {
      transition: all 0.5s;
    }
    &:hover {
      svg path {
        fill-opacity: 1;
      }
    }
  }
}
.text30 {
  font-size: 30px;
  line-height: 120%;
  font-weight: 600;
  letter-spacing: -0.6px;
  color: #ffffff;
}
.text22 {
  font-family: 'Sora';
  font-size: 22px;
  line-height: 120%;
  font-weight: 600;
  letter-spacing: -0.44px;
  color: #EAEAEA;
}
.text20 {
  font-size: 20px;
  line-height: 120%;
  font-weight: 600;
  color: #ffffff;
}
.content-inner {
  display: flex;
  justify-content: space-between;
  min-height: 830px;
  margin-top: 40px;
  border-top: 1px solid #1B1B1B;
}
.sidebar {
  width: 272px;
  background: #121212;
  padding: 40px 25px;
  position: relative;
  overflow: hidden;
  &-bg {
    span:nth-child(1) {
      content: '';
      position: absolute;
      top: 0;
      right: -420px;
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: rgba(31, 188, 255, 0.19);
      filter: blur(107px);
      pointer-events: none;
    }
    span:nth-child(2) {
      content: '';
      position: absolute;
      left: -250px;
      bottom: 0;
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: rgba(31, 188, 255, 0.19);
      filter: blur(107px);
      pointer-events: none;
    }
    span:nth-child(3) {
      content: '';
      position: absolute;
      left: -250px;
      bottom: -250px;
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: rgba(31, 188, 255, 0.19);
      filter: blur(107px);
      pointer-events: none;
    }
  }
}
.content-right {
  width: calc(100% - 272px);
  padding: 60px 0 60px 100px;
}
.tab {
  font-size: 16px;
  line-height: 120%;
  color: #FFF;
  cursor: pointer;
  transition: all 0.5s;
  position: relative;
  &:hover {
    color: #5B94FF;
  }
  &--active {
    color: #5B94FF;
  }
  &:not(:first-child) {
    margin-top: 27px;
    &:after {
      content: '';
      position: absolute;
      top: -13px;
      left: 0;
      width: 100%;
      height: 1px;
      background: rgba(255, 255, 255, 0.10);
    }
  }
}
.tabs__content {
  display: none;
  &--active {
    display: block;
  }
}
.modal {
  background: rgba(0, 0, 0, 0.90);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 630px;
    max-width: 100%;
    position: relative;
    background: #151515;
    border: 1px solid rgba(255, 255, 255, 0.10);
    margin: 0 auto;
    padding: 50px 60px;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 18px;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    color: #A9A9A9;
    text-align: center;
  }
  &__subtitle {
    font-size: 30px;
    line-height: 120%;
    font-weight: 600;
    letter-spacing: -0.6px;
    color: #E1E1E1;
    text-align: center;
    margin-top: 10px;
  }
  &__img {
    margin-top: 30px;
    img {
      width: 100%;
    }
  }
  &__text {
    margin-top: 30px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: #ffffff;
    }
  }
  &__btns {
    margin-top: 30px;
  }
  &__btn {
    width: 245px;
    height: 60px;
    margin: 0 auto;
  }
  &__cancel {
    font-size: 18px;
    line-height: 120%;
    color: #E1E1E1;
    cursor: pointer;
    text-align: center;
    margin-top: 30px;
    transition: all 0.5s;
    &:hover {
      color: #ffffff;
    }
  }
  .domains-main__info-item {
    width: 11px;
    height: 11px;
    margin-top: 4px;
    position: relative;
  }
  .dashboard-card__active-show {
    color: rgba(255, 255, 255, 0.30);
    cursor: pointer;
    span {
      border-color: rgba(255, 255, 255, 0.30);
      margin: 0;
    }
  }

}
.modal2 {
  .modal__dialog {
    width: 850px;
  }
  .modal__subtitle {
    margin: 0 0 10px 0;
  }
}
.nice-select {
  height: 50px;
  display: flex;
  align-items: center;
  background: transparent !important;
  border: 1px solid #313133;
  border-radius: 0;
  padding: 5px 32px 5px 20px;
  &:after {
    top: 50%;
    right: 18px;
    transform: translate(0, -50%);
    width: 11px;
    height: 5px;
    background-image: url("../img/select-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  .current {
    font-size: 14px;
    line-height: 120%;
    color: #ffffff;
  }
  .list {
    width: 100%;
    background: #323232;
    filter: drop-shadow(0px 4px 40px rgba(0, 0, 0, 0.70));
    padding: 20px 15px;
    .selected {
      font-weight: 400;
      background: transparent;
      opacity: 0.5;
    }
    li {
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      border: none;
      &:hover {
        background: transparent;
        opacity: 0.5;
      }
      &:focus {
        background: transparent;
        opacity: 0.5;
      }
    }
  }
}
.nice-select.open:after {
  transform: rotate(-180deg);
}
.option.focus, .nice-select .option.selected.focus {
  background: transparent;
}
table {
  width: 100%;
  border-spacing: 0;
  margin-top: 16px;
  thead {
    background: #000000;
    tr {
      th {
        text-align: start;
        font-size: 14px;
        line-height: 120%;
        font-weight: 700;
        color: #ffffff;
        padding: 12px 5px;
        &:first-child {
          padding-left: 14px;
        }
        &:last-child {
          padding-right: 14px;
        }
      }
    }
  }
  tbody {
    tr {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background: rgba(255, 255, 255, 0.15);
      }
      td {
        text-align: start;
        font-size: 14px;
        line-height: 120%;
        font-weight: 700;
        color: #ffffff;
        padding: 12px 5px;
        &:first-child {
          padding-left: 14px;
        }
        &:last-child {
          padding-right: 14px;
        }
      }
    }
  }
}
.beginning-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0 0 0;
  border-bottom: 2px solid #434343;
  &__left {
    margin: 0 30px 10px 0;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  &__group {
    display: flex;
    align-items: center;
    position: relative;
    &:not(:last-child) {
      margin-right: 50px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -25px;
        transform: translate(0, -50%);
        width: 1px;
        height: 100%;
        background: #343434;
      }
    }
  }
  &__link {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-left: 12px;
    }
    i {
      display: flex;
      align-items: center;
      max-width: 13px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.5s;
      }
    }
    span {
      font-size: 12px;
      line-height: 120%;
      font-weight: 600;
      letter-spacing: 0.6px;
      text-transform: uppercase;
      color: #7C7C7C;
      margin-left: 5px;
      transition: all 0.5s;
    }
    &:hover {
      i svg path {
        stroke: #ffffff;
      }
      i svg circle {
        stroke: #ffffff;
      }
      .beginning-block__icon path {
        fill: #B8B8B8;
      }
      span {
        color: #ffffff;
      }
    }
  }
}
.change {
  &__bg {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.60);
    backdrop-filter: blur(2.5px);
    transition: all 0.5s;
    opacity: 0;
    pointer-events: none;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__menu {
    position: fixed;
    z-index: 5;
    top: 0;
    right: -105%;
    bottom: 0;
    width: 460px;
    height: 100%;
    background: #0C0C0C;
    transition: all 0.5s;
    &--active {
      right: 0;
    }
  }
  &__menu-wrap {
    height: 100%;
    padding: 24px 24px 40px 24px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.1);;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #ffffff;
      border-radius: 3px;
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__info {
    display: flex;
    align-items: center;
    font-size: 10px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #434343;
    span:nth-child(2) {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 11px;
      height: 11px;
      border: 1px solid #4D4D4D;
      border-radius: 50%;
      margin-left: 6px;
    }
  }
  &__close {
    width: 18px;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
  }
  &__items2 {
    .change__item {
      border: none !important;
    }
  }
  &__item {
    display: grid;
    grid-template-columns: 0.7fr 1.3fr;
    gap: 30px;
    align-items: center;
    padding: 10px 0;
    &:not(:last-child) {
      border-bottom: 1px solid #2F2F31;
    }
    &--100 {
      grid-template-columns: 100%;
      gap: 12px;
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
      color: #7C7C7C;
      i {
        max-width: 11px;
        margin-right: 5px;
        svg {
          max-width: 100%;
          height: auto;
        }
      }
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      color: #CDCDCD;
    }
    &-input {
      input {
        width: 100%;
        height: 40px;
        font-size: 16px;
        line-height: 120%;
        color: #fff;
        background: rgba(255,255,255,.1);
        border: 1px solid transparent;
        padding: 5px 20px;
        background-image: url(../img/input-icon.svg);
        background-repeat: no-repeat;
        background-position: top 50% right 20px;
        background-size: 6px;
        &:focus {
          background-image: none;
        }
      }
    }
    &-textarea {
      textarea {
        width: 100%;
        height: 111px;
        font-size: 16px;
        line-height: 120%;
        color: #fff;
        background: rgba(255,255,255,.1);
        border: 1px solid transparent;
        padding: 15px 20px;
        background-image: url(../img/input-icon.svg);
        background-repeat: no-repeat;
        background-position: top 15px right 20px;
        background-size: 6px;
        resize: none;
        &:focus {
          background-image: none;
        }
      }
    }
  }
  &__value {
    display: grid;
    grid-template-columns: repeat(10, calc(100% / 10));
    border: 1px solid #282828;
    &-item {
      display: flex;
      align-items: center;
      position: relative;
      &:not(:last-child) {
        border-right: 1px solid #282828;
      }
      &--active {
        background: #FF524C;
      }
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label {
          background: #0055F6;
        }
      }
      label {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 24px;
        padding: 2px;
        font-size: 14px;
        line-height: 105%;
        color: #ffffff;
      }
    }
  }
  &__select .nice-select {
    width: auto;
    height: auto;
    padding: 0 32px 0 0;
    border: none;
    .list {
      width: auto;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-top: 30px;
  }
  &__btn {
    width: 120px;
    height: 40px;
  }
  &__cancel {
    font-size: 16px;
    line-height: 120%;
    color: #E1E1E1;
    margin-left: 30px;
    transition: all 0.5s;
    cursor: pointer;
    &:hover {
      opacity: 0.5;
    }
  }
}
.radar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  min-height: 100%;
  #SvgjsText1009 {
    display: none;
  }
  #SvgjsText1344 {
    display: none;
  }
  .apexcharts-toolbar {
    display: none;
  }
  #SvgjsText1044,
  #SvgjsText1045,
  #SvgjsText1046,
  #SvgjsText1047 {
    display: none;
  }
}
