.knowledge {
  padding-top: 40px;
  .tabs__content {
    max-width: 782px;
  }
  &__search {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;
    input {
      width: calc(100% - 179px);
      height: 60px;
      background: rgba(255, 255, 255, 0.10);
      border: none;
      padding: 5px 18px;
      font-size: 16px;
      line-height: 120%;
     color: #ffffff;
      &::placeholder {
        color: #A6A6A6;
      }
    }
    button {
      width: 179px;
      height: 60px;
    }
  }
  &__subtitle {
    margin-bottom: 35px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    border: 1px solid #151515;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    &:not(:last-child) {
      border-bottom: 1px solid #151515;
    }
    &:hover {
      background: rgba(255, 255, 255, 0.06);
      .knowledge__item-title {
        color: #ffffff;
      }
      .knowledge__item-icon svg path {
        fill-opacity: 1;
      }
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      color: #A9A9A9;
      transition: all 0.5s;
    }
    &-icon {
      flex-shrink: 0;
      width: 7px;
      margin-left: 30px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.5s;
      }
    }
  }
}
.concepts {
  &__link {
    display: inline-flex;
    align-items: center;
    margin-bottom: 16px;
    i {
      flex-shrink: 0;
      width: 6px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.5s;
      }
    }
    span {
      font-size: 14px;
      line-height: 120%;
      color: rgba(255, 255, 255, 0.40);
      margin-left: 5px;
      transition: all 0.5s;
    }
    &:hover {
      i svg path {
        fill-opacity: 1;
      }
      span {
        color: #ffffff;
      }
    }
  }
}
.concepts-desc {
  margin-top: 35px;
  &__group:not(:first-child) {
    margin-top: 30px;
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    color: #ffffff;
  }
  &__text {
    margin-top: 16px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: #ffffff;
    }
  }
}
.concepts-info {
  margin-top: 30px;
  &__title {
    font-size: 20px;
    line-height: 120%;
    color: #ffffff;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
    color: #ffffff;
    margin-top: 24px;
  }
  .item {
    position: relative;
    .hide {
      left: -12px;
    }
  }
  &__table {
    tbody {
      tr {
        td:first-child {
          width: 120px;
        }
      }
    }
  }
}
.concepts-modes {
  margin-top: 30px;
  &__title {
    font-size: 20px;
    line-height: 120%;
    color: #ffffff;
  }
  &__text {
    font-size: 16px;
    line-height: 140%;
    color: #FFF;
    margin-top: 24px;
  }
  &__bottom {
    margin-top: 24px;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
  }
  &__info {
    margin-top: 16px;
  }
  &__info-title {
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
  }
  &__info-text {
    font-size: 18px;
    line-height: 120%;
    color: #ffffff;
    margin-top: 8px;
  }
}
.concepts-examples {
  margin-top: 30px;
  &__title {
    font-size: 20px;
    line-height: 120%;
    color: #ffffff;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    margin-top: 24px;
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
    color: #ffffff;
    margin-top: 8px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 16px;
  }
  &__item-top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: start;
    background: #000000;
    padding: 12px 14px 0 14px;
  }
  &__item-title {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    margin-right: 30px;
    margin-bottom: 12px;
  }
  &__item-info {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.50);
    margin-bottom: 12px;
  }
  &__item-desc {
    border-top: 1px solid transparent;
    border: 1px solid rgba(255, 255, 255, 0.15);
    padding: 10px 14px;
    p {
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
    }
  }
}
.concepts-info2 {
  margin-top: 30px;
  &__title {
    font-size: 20px;
    line-height: 120%;
    color: #ffffff;
  }
  &__items {
    margin-top: 24px;
  }
  &__item {
    &:not(:first-child) {
      margin-top: 24px;
    }
  }
}