}
.tariff2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border: 1px dashed #454545;
  padding: 25px 25px 10px 25px;
  margin-top: 25px;
  &__left {
    margin: 0 30px 10px 0;
  }
  &__right {
    margin-bottom: 10px;
  }
  &__title {
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    color: #FFF;
  }
  &__info {
    font-size: 14px;
    line-height: 120%;
    color: #7D7D7D;
    margin-top: 10px;
  }
  &__term {
    font-size: 16px;
    line-height: 120%;
    color: #FFF;
  }
}
.tariff-table__wrap {
  margin-top: 40px;
  overflow-y: auto;
  &::-webkit-scrollbar {
    height: 3px;
  }
  &::-webkit-scrollbar-track {
    background: #151515;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #ffffff;
    border-radius: 3px;
  }
}
.tariff-table {
  width: 1068px;
  margin-top: 0;
  thead {
    background: transparent;
  }
  tbody {
    tr {
      td {
        font-size: 12px;
      }
      td:not(:first-child) {
        text-align: center;
      }
    }
  }
  &__group {
    &:after {
      display: none;
    }
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    color: #ffffff;
    text-align: center;
    span {
      display: block;
      font-size: 12px;
      font-weight: 700;
      text-transform: uppercase;
      margin-top: 5px;
    }
  }
  &__subtitle {
    font-size: 12px;
    color: #6D6D6D;
  }
  &__btn {
    width: 118px;
    height: 37px;
    background: transparent;
    border: 1px solid #0055F6;
    margin: 15px auto 0;
    span {
      font-size: 14px;
      line-height: 120%;
      color: #FFF;
    }
    &:hover {
      background: #0055F6;
    }
  }
  .active-cell {
    background: #151515;
    .tariff-table__btn {
      background: #0055f6;
    }
  }
}
.payment {
  max-width: 781px;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 25px;
    margin-top: 25px;
  }
  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border: 1px dashed #454545;
    padding: 20px 20px 0 20px;
    &--active {
      .payment__item-title {
        color: #FFF;
      }
      .payment__item-btn {
        background: #0055F6;
        span {
          color: #ffffff;
        }
        span:nth-child(1) {
          display: none;
        }
        span:nth-child(2) {
          display: block;
        }
      }
    }
    &-desc {
      margin: 0 30px 20px 0;
    }
    &-title {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: rgba(255, 255, 255, 0.53);
    }
    &-checkboxes {
      display: flex;
      align-items: center;
      margin-top: 12px;
    }
    &-checkbox {
      display: flex;
      align-items: center;
      position: relative;
      &:not(:first-child) {
        margin-left: 30px;
      }
      input {
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 35px;
        height: 18px;
        border-radius: 20px;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
        &:checked ~ label {
          background: #0055F6;
          &:after {
            left: calc(100% - 15px);
            background: #ffffff;
          }
        }
        &:checked ~ .payment__checkbox-title {
          span {
            color: #ffffff;
          }
          span:nth-child(1) {
            display: block;
          }
          span:nth-child(2) {
            display: none;
          }
        }
        &:checked ~ .payment__checkbox-text {
          color: #ffffff;
        }
      }
      label {
        flex-shrink: 0;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 35px;
        height: 18px;
        background: #000000;
        border-radius: 20px;
        transition: all 0.5s;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 3px;
          transform: translate(0, -50%);
          width: 12px;
          height: 12px;
          background: #343434;
          border-radius: 50%;
          transition: all 0.5s;
        }
      }
    }
    &-btn {
      height: 30px;
      padding: 5px 20px;
      background: #151515;
      margin-bottom: 20px;
      span {
        font-size: 16px;
        line-height: 120%;
        font-weight: 600;
        color: #A9A9A9;
      }
      span:nth-child(2) {
        display: none;
      }
    }
  }
  &__checkbox-title {
    min-width: 25px;
    margin-left: 40px;
    span {
      font-size: 14px;
      line-height: 120%;
      color: rgba(255, 255, 255, 0.53);
    }
    span:nth-child(1) {
      display: none;
    }
  }
  &__checkbox-text {
    font-size: 14px;
    line-height: 120%;
    color: rgba(255, 255, 255, 0.53);
    margin-left: 15px;
  }
}
.payment-history {
  table {
    thead {
      tr {
        th {
          color: #848484;
        }
      }
    }
    tbody {
      tr {
        td {
          color: #848484;
        }
      }
    }
  }
  &__download {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 13px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      margin-left: 3px;
    }
  }
  .table-nodata {
    tbody {
      height: 186px;
      tr {
        td {
          opacity: 0;
          pointer-events: none;
        }
        .transparency {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
  }
}